<template>
  <form @submit.prevent="submit">
    <!-- E-mails e Telefones -->
    <div class="card">
      <div class="card-header">
        <h5 class="card-title">Informações de Contato</h5>
      </div>
      <div class="card-body">
        <h5 class="text-center">E-mails</h5>
        <!-- Email Primário -->
        <div class="form-group">
          <label for="ipt-email1">E-mail Principal</label>
          <input id="ipt-email1" type="email" class="form-control" maxlength="45" v-model="email1" required>
        </div>
        <!-- Email Secundário -->
        <div class="form-group" v-if="qtdEmails >= 2">
          <label for="ipt-email2">E-mail Secundário</label>
          <div class="input-group">
            <input id="ipt-email2" type="email" class="form-control" maxlength="45" v-model="email2" :class="{'is-invalid': email2 && (email2 === email1 || email2 === email3)}">
            <div class="input-group-append" v-if="qtdEmails === 2">
              <button class="btn btn-danger" type="button" @click="()=>{qtdEmails--; email2='';}"><i class="fas fa-minus"></i></button>
            </div>
            <div class="invalid-feedback">E-mail repetido!</div>
          </div>
        </div>
        <!-- Email Terciário -->
        <div class="form-group" v-if="qtdEmails >= 3">
          <label for="ipt-email3">E-mail Terciário</label>
          <div class="input-group">
            <input id="ipt-email3" type="email" class="form-control" maxlength="45" v-model="email3" :class="{'is-invalid': email3 && (email3 === email1 || email3 === email2)}">
            <div class="input-group-append">
              <button class="btn btn-danger" type="button" @click="()=>{qtdEmails--; email3='';}"><i class="fas fa-minus"></i></button>
            </div>
            <div class="invalid-feedback">E-mail repetido!</div>
          </div>
        </div>
        <!-- Botão Add-Email -->
        <button v-if="qtdEmails < 3" class="btn btn-block btn-info" type="button" @click="qtdEmails++"><i class="fas fa-plus-square pr-2"></i>Adicionar outro e-mail</button>

        <h5 class="text-center border-top border-secondary mt-3 pt-3">Telefones</h5>
        <p v-if="telefones.length===0" class="text-center mb-2">não há telefone registrado</p>
        <template v-for="(telefone, indice) in telefones">
          <div class="form-group" :key="indice">
            <div class="input-group">
              <!-- Prepend [icone] -->
              <div class="input-group-prepend">
                <div class="input-group-text">
                  <i v-if="telefone.tipo===1" class="fas fa-home"></i>
                  <i v-else-if="telefone.tipo===2" class="fas fa-briefcase"></i>
                  <i v-else-if="telefone.tipo===3" class="fas fa-mobile-alt px-1"></i>
                </div>
              </div>
              <!-- Input [numero] -->
              <the-mask type="tel" class="form-control" :mask="['(##) ####-####', '(##) #####-####']" v-model="telefones[indice].numero" required></the-mask>
              <!-- Append [remover] -->
              <div class="input-group-append">
                <button class="btn btn-danger" type="button" @click="telefones.splice(indice, 1)"><i class="fas fa-minus"></i></button>
              </div>
            </div>
            <small class="text-muted" v-if="telefone.tipo===1">fixo residencial</small>
            <small class="text-muted" v-else-if="telefone.tipo===2">fixo comercial</small>
            <small class="text-muted" v-else-if="telefone.tipo===3">celular</small>
          </div>
        </template>
        <!-- Botão Add-Telefone -->
        <div class="dropdown">
          <button class="btn btn-block btn-info dropdown-toggle" type="button" data-toggle="dropdown"><i class="fas fa-plus-square pr-2"></i>Adicionar um telefone</button>
          <div class="dropdown-menu">
            <button class="dropdown-item" type="button" @click="addPhone(1)"><i class="fas fa-home pr-2"></i>Fixo Residencial</button>
            <button class="dropdown-item" type="button" @click="addPhone(2)"><i class="fas fa-briefcase pr-2"></i>Fixo Comercial</button>
            <button class="dropdown-item" type="button" @click="addPhone(3)"><i class="fas fa-mobile-alt pr-2 pl-1"></i>Celular</button>
          </div>
        </div>
      </div>
      <div class="card-footer">
        <button type="submit" class="btn btn-block btn-primary">AVANÇAR</button>
        <router-link :to="{name: 'recad-form-endereco'}" class="btn btn-block btn-secondary">VOLTAR</router-link>
      </div>
    </div>
  </form>
</template>

<script>
  export default {
    name: "Contato",
    data: () => ({
      qtdEmails: 1,
      email1: '',
      email2: '',
      email3: '',
      telefones: [] // {tipo: Number, numero: String} | 1-residencial,2-comercial,3-celular
    }),
    created() {
      //Alimentar campos
      if (!sessionStorage.getItem('recad_contato')) {
        //Carrega da Store
        if (this.$store.state.email1) this.email1 = this.$store.state.email1;
        if (this.$store.state.email2) {this.email2 = this.$store.state.email2; this.qtdEmails++;}
        if (this.$store.state.email3) {this.email3 = this.$store.state.email3; this.qtdEmails++;}
        this.telefones = this.$store.state.telefones;
      } else {
        //Carrega da sessionStorage
        const dados = JSON.parse(sessionStorage.getItem('recad_contato'));
        this.email1 = dados.email1;
        this.email2 = dados.email2;
        this.email3 = dados.email3;
        this.telefones = dados.telefones;
        this.qtdEmails = dados.qtdEmails;
      }
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.setItem('recad_contato', JSON.stringify({
        email1: this.email1,
        email2: this.email2,
        email3: this.email3,
        telefones: this.telefones,
        qtdEmails: this.qtdEmails
      }));
      next();
    },
    methods: {
      addPhone(type) {
        const quantidadeAtual = this.telefones.filter(telefone => telefone.tipo===type).length;
        if (quantidadeAtual >= 3) {
          this.$toastr.info("Você atingiou o limite de 3 telefones por categoria");
          return;
        }
        this.telefones.push({tipo: type, numero: ''});
      },
      validarTelefones() {
        //verifica se há número repetido
        if (!this.telefones.length) {
          this.$toastr.warning('Adicione um telefone');
          return false;
        }
        const numeros = this.telefones.map(telefone => telefone.numero);
        const repetidos = numeros.filter(num => {
          return numeros.filter(numero => num === numero).length > 1;
        });
        if (repetidos.length > 0) {
          this.$toastr.warning('Há telefones repetidos');
          return false;
        }
        if (!this.telefones.filter(telefone => telefone.tipo===3).length) {
          this.$toastr.warning('Você precisa registrar um celular');
          return false;
        }
        return true;
      },
      validarEmails() {
        //valida se há pelo menos um email
        if (this.qtdEmails === 1 && !this.email1) {
          this.$toastr.warning("Insira pelo menos um e-mail");
          return false;
        }
        //valida se há email repetido
        if ((this.email1 && this.email2 && this.email1===this.email2) || (this.email1 && this.email3 && this.email1===this.email3) || (this.email2 && this.email3 && this.email2===this.email3)) {
          this.$toastr.warning("Há e-mails repetidos");
          return false;
        }
        //re-organiza caso tenha preenchido email terciario, mas deixado secundario vazio
        if (!this.email2 && this.email3) {
          this.email2 = this.email3;
          this.email3 = '';
          this.qtdEmails--;
        }
        return true;
      },
      submit() {
        if (!this.validarTelefones() || !this.validarEmails()) return;
        this.$router.push({name: 'recad-form-foto'});
      }
    }
  }
</script>

<style scoped>

